import { log } from './ui';

export async function captureAudio() {
  log(`Solicitando acceso a micrófono...`);
  const result = await navigator.mediaDevices.getUserMedia({ audio: true });
  log('Permiso de audio concedido');
  return result;
}

export async function playAudio(stream: MediaStream) {
  const audio = prepareAudio(stream);
  await audio.play();
  console.log('playing');
}

function prepareAudio(stream: MediaStream) {
  const audio = document.createElement('audio');
  audio.srcObject = stream;
  document.body.appendChild(audio);
  return audio;
}
